import styled from 'styled-components';
import { device } from '../../responsive-sizez';

export const HeaderTimeline = styled.div `
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: .99rem;
    content: '';
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    z-index: 1021;
    background-image: url(data:image/svg+xml;base64,)
`
export const PageContainer = styled.div`
    background-color: #fff;
    margin: 0 auto;
    max-width: 120rem;
    position:relative;
`
export const HeaderLogos = styled.div`
    position:absolute;
    top:1rem;
    left:0;
    width:100%;
    height:4rem;
    background-color:#fff;
    display:flex;
    align-content:center;
`
export const Logo = styled.img`
    width:11rem;
`
export const LogoCdn = styled.img`
    padding-top:.5rem;
    width:14rem;
    height:3rem;
    margin:0 1rem;
`
export const StripHeader = styled.div`
    width:100%;
    position:absolute;
    top:calc(60vh + 5rem);
    z-index:-1;
    height:16vh;
    background:#fff;

    @media ${device.laptop_1024px} {
        top:calc(50vh + 5rem);
    }

    @media ${device.mobileL_425px} {
        top:calc(30vh + 5rem);
    }
`
export const StripHeaderHeading = styled.div`
        background: linear-gradient(to right, #007bc0, #008ecf);
        width:100%;
        height:7rem;
        position:absolute;
        top:5vh;
        text-align:center;
        padding: 1rem 0;

        @media ${device.mobileL_425px} {
            top:6vh;
        }
`
export const StripHeaderHeadingText = styled.h1`
        font-family:Arial;
        color:#fff;
        font-size:2rem;
        width:80%;
        margin: 0 auto;
        font-weight:100;
        @media ${device.mobileL_425px} {
            font-size:1.5rem;
            width:95%;
    }
`
export const SectionsContainer = styled.div`
    width:100%;
    position:absolute;
    background:#fff;
    top:calc(60vh + 5rem + 16vh);

    @media ${device.laptop_1024px} {
        top:calc(50vh + 5rem + 16vh);
    }

    @media ${device.mobileL_425px} {
        top:calc(30vh + 5rem + 16vh);
    }
`
export const Section = styled.div`
    width:100%;
    background:#fff;
`
export const SectionMax = styled.div`
    margin:0 auto;
    width:80%;
    display:flex;
    flex-direction:column;

    @media ${device.laptop_1024px} {
        width:100%;
    }

    @media ${device.mobileL_425px} {
        width:100%;
    }
`
export const SectionHeader = styled.div`
    width:100%;
    display:flex;
    flex-direction: column;
    justify-content:center;
    align-items: center;
    margin:2rem 1rem;

    @media ${device.mobileL_425px} {
        margin:0 auto;
        text-align:center;
    }
`
export const SectionHeading = styled.h1`
    margin:1rem 0;
    font-size:3.2rem;
`
export const SectionText = styled.p`
    font-size:1.5rem;
    font-weight:600;
`
export const SectionImgContainer = styled.div`
    width:100%;
    display: flex;
    flex-direction:row;
    justify-content:center;

    @media ${device.laptop_1024px} {
        flex-wrap: wrap;
    }
`
export const ContactUsWrapper = styled.div`
    display:block;
    background: linear-gradient(to right, #008ecf, #00a8b0);
    border-color: #fff;
    color:#fff;
    font-family:  Helvetica, Arial, sans-serif;
    font-size:1.2rem;
    width:100%;
    height:20vh;
    margin-bottom:5vh;
    position:relative;

    @media ${device.mobileL_425px} {
        height:25vh;
    }

    @media ${device.mobileL_425px} and (max-height: 667px) {
        height:40vh;
        padding-top:.2rem;
    }
`

export const ContactUsContainer = styled.div`
    width:80%;
    height:15vh;
    position:absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    display:flex;
    justify-content:space-around;
    align-items:center;
    padding:3rem;

    
    @media ${device.mobileL_425px} {
        flex-direction: column;
        margin:1rem;
        width:100%;
        position:relative;
        font-size:1.6rem;
        align-items:flex-start;
        padding:1rem;
        height:15vh;
    }
`

export const ContactUsLeft = styled.div`
    line-height:2.5rem;

    @media ${device.mobileL_425px} {
        margin-bottom:1rem;
    }
    @media ${device.tablet_768px} and (max-height: 721px) {
        margin-left:2rem;
    }
`

export const ContactUsLine = styled.div`
    width:1px;
    height:15vh;
    background:#fff;
    opacity:.5;

    @media ${device.mobileL_425px} {
        height:1px;
        width:92%;
        position:absolute;
    }
`

export const ContactUsRight = styled.div`
    line-height:2.5rem;

    @media ${device.tablet_768px} and (max-height: 721px) {
        margin-left:2rem;
    }
`

export const  ContactUsContent = styled.div`
    display:flex;
    flex-direction: column;
    width:100%;
`

export const ContactUsContentHeading = styled.h4`
    font-size:2rem;
    margin-bottom:1rem;
`

export const ContactUsContentRow = styled.div`

`
export const ContactUsIconLink = styled.a`
    text-decoration:none;
    background-color:transparent;
    cursor:pointer;
    &:focus, &:hover, &:visited, &:link, &:active {
        text-decoration: none;
    }
    color:#fff;
`

export const ContactUsIconSpan = styled.span`
    cursor:pointer;
    margin-right:10px;
`
